import Vue from 'vue'
import Vuex from 'vuex'
import persistedState from 'vuex-persistedstate'
Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        ifLogin: false,
        ifHide:false,
        account: ''
    },
    mutations: {
        login(state,account){
            state.ifLogin=true;
            state.account=account;
        },
        logout(state){
            state.ifLogin=false;
            state.account='';
        },
        changeHideStatus(state,ifHide){
            console.log(ifHide)
            if(ifHide){
                state.ifHide=false;
            } else{
                state.ifHide=true;
            }
        }
    },
    actions: {},
    modules: {},
    plugins: [persistedState({ storage: window.sessionStorage})]
})
