<template>
    <div class="topBar">
        <img src="http://qa3wvi1lp.bkt.clouddn.com/WechatIMG297.jpeg" style="width:50px;cursor:pointer;"
             @click="goHome"/>
        <el-button type="warning" icon="el-icon-edit-outline" circle @click="goTodoList"
                   style="width:50px;height:50px;"></el-button>
        <el-button @click="$router.push('blogList')">blogList</el-button>
        <div>
            <span>开启隐藏模式：</span>
            <el-switch
                    v-model="ifHide"
                    @change="chagneHideStatus"
                    active-color="#13ce66"
                    inactive-color="#ff4949">
            </el-switch>
        </div>
        <img src="../assets/images/logout.png" style="width:60px;cursor:pointer;" @click="logout"/>
    </div>
</template>
<script>
    import {mapState} from 'vuex'

    export default {
        data() {
            return {}
        },
        computed:
            mapState({
                ifHide: state => state.ifHide,
            }),
        methods: {
            chagneHideStatus() {
                this.$store.commit('changeHideStatus', this.ifHide)
            }
            ,
            goTodoList() {
                this.$router.push('todoList')
            }
            ,
            goHome() {
                this.$router.push('home')
            }
            ,
            logout() {
                this.$store.commit('logout')
                this.$router.push('/')
            }
        }
    }
</script>
<style lang="less" scoped>
    .topBar {
        display: flex;
        height: 50px;
        justify-content: space-between;
        align-items:center;
        /*background:grey;*/
    }
</style>
