import Vue from 'vue'
import VueRouter, {RouteConfig} from 'vue-router'
import store from '../store/index'

Vue.use(VueRouter)

const routes: Array<RouteConfig> = [
    {
        path: '/',
        name: 'login',
        component: () => import('../views/Login.vue')
    },
    {
        path: '/home',
        name: 'home',
        component: () => import('../views/Home.vue'),
        meta:{
            keepAlive:true
        }
    },
    {
        path: '/artical',
        name: 'artical',
        component: () => import('../views/Artical.vue')
    },
    {
        path: '/todoList',
        name: 'todoList',
        component: () => import('../views/TodoList.vue')
    },
    {
        path: '/blog',
        name: 'blog',
        component: () => import('../views/Blog.vue')
    },
    {
        path: '/blogList',
        name: 'blogList',
        component: () => import('../views/BlogList.vue')
    }

]


const router = new VueRouter({
    routes
})
router.beforeEach((to,from,next) => {
    const isLogin = store.state.ifLogin;
    console.log('to', to.name)
    console.log('from', from.name)

    const nextRoute = ['home', 'artical', 'todoList']   // 需要登录的页面
    if (nextRoute.indexOf(String(to.name)) >= 0) {              // 检测是否登录的页面
        console.log('到这里了')
        console.log(isLogin)
        if (!isLogin) {                                 // 如果未登录（本地存储无用户数据），并且要跳到登录页面
                console.log('那就是这里出问题了')
                next('/')
                return
        } else {
            console.log('这里出问题了')
            next()
        }
    } else {
        console.log('还是这里出问题了')
        next()
    }
})
export default router
